.allmovies {
	margin-top: 24px;

	&-length {
		font-style: normal;
		font-weight: 600;
		font-size: 32px;
		line-height: 40px;
		letter-spacing: -0.02em;
		color: #767e94;
		margin-bottom: 24px;
	}

	&-card {
		display: grid;
		grid-template-columns: repeat(4, minmax(0, 1fr));
		grid-row-gap: 24px;
		row-gap: 24px;
		grid-column-gap: 24px;
		column-gap: 24px;

		&-wrapper {
			cursor: pointer;
			overflow: hidden;

			&:hover {
				.allmovies-card-poster {
					transform: scale(1.1);
				}
			}
		}

		&-content {
			padding: 0.5rem;
			background: rgba(32, 40, 62, 0.8);
			backdrop-filter: blur(40px);
			border-radius: 12px;
			overflow: hidden;
		}

		&-poster {
			margin-bottom: 24px;
			transition: transform 0.2s ease-in-out;
		}

		&-name {
			padding: 0.5rem;
			margin-bottom: 0.5rem;
			font-style: normal;
			font-weight: 600;
			font-size: 16px;
			line-height: 24px;
			letter-spacing: 0.02em;
			color: #ebeef5;
			text-decoration: none;
		}
	}
}
